export default {
  layout: "Emp-content",
  mounted() {
    this.getEmployee();

  },
  data() {
    return {
      employees: [],
      errors: {},
    };
  },
  methods: {
    getEmployee() {
      this.$axios
        .$get("/employee/show")
        .then((res) => {
          this.employees = res;
        })
        .catch((err) => {
          console.log(err);
        });
    },    
    employeeDelete(id) {
      if (confirm("Are you sure to delete this employee?")) {
        this.$axios
          .$get("/employee/delete/" + id)
          .then((res) => {
            this.getEmployee();
            this.$toaster.error(res.message);
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
    employeeStatus(id, status) {
      this.$axios
        .$get("/employee/status/" + id)
        .then((res) => {
          this.getEmployee();
          this.$toaster.success(res.message);
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
